@import url("//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap.min.css");

.Curriculo {
    h1 { color: #606060; font-size: 25px;}
    .box-curriculo {
        display: flex;
        flex-wrap: wrap;

        .box-left {
            border: 0px solid #000;
            .topico {
                border-top: 1px solid #d5d5d5;
                display: flex;
                padding: 30px 0;
                .circle {
                    border: 0px solid red;
                    width: 100px;
                    height: 100px;
                    background: #404040;
                    color: #ffffff;
                    font-size: 13px;
                    display: flex;
                    text-align: center;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    padding: 0 3px;
                }
                .texto {
                    border: 0px solid red;
                    max-width: 620px;
                    padding: 15px;
                    color: #606060;
                    .entidade-estudo {
                        display: flex;
                        align-items: baseline;
                        i {
                            padding-right: 5px;
                        }
                    }
                    .empresa {
                        font-size: 15px;
                        font-weight: 600;
                        
                        .dados-empresa {
                            display: flex;
                            .tit-empresa {
                                width: 85px;
                            }
                        }
                        .dados-cargo {
                            display: flex;
                            .tit-cargo {
                                width: 85px;
                            }
                        }
                        .dados-periodo {
                            display: flex;
                            .tit-periodo {
                                width: 85px;
                            }
                        }
                        .tit-atividades {
                            padding-top: 10px;
                            color: #e83556;
                            font-size: 16px;
                        }
                        .box-atividades {
                            padding: 20px 0;
                            .atividade {
                                display: flex;
                                align-items: baseline;
                                i {
                                    padding-right: 5px;
                                }
                                p {
                                    font-size: 13px;
                                    font-weight: 500;
                                }
                            }
                        }
                    }
                }
            }

            .box-topico {
                border: 1px solid #000;
                display: flex;
                border-top: 1px solid #d5d5d5;
                padding: 20px 0;
                .box-circle {
                    border: 1px solid red;
                    width: 100px;
                    height: 100px;
                    .circle {
                        border-radius: 50%;
                        background: #404040;
                        width: 100px;
                        height: 100px;
                        font-size: 13px;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        color: #ffffff;
                        padding: 0 3px;
                    }
                }
                .box-texto {
                    border: 1px solid red;
                    max-width: 620px;
                    color: #606060;
                    font-size: 13px;
                }
            }
        }

        .box-right {
            border: 0px solid blue;
            flex: 1;
            .box-topico2 {
                padding: 0 25px;
                .icon {
                    width: 50px;
                    height: 50px;
                    background: #e83556;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: #ffffff;
                }
                .title {
                    border-bottom: 1px solid #d5d5d5;
                    font-size: 18px;
                    color: #606060;
                    padding: 5px 0;
                }
                .text {
                    padding: 15px 0 30px;
                    color: #606060;
                    li {
                        list-style: none;
                        padding: 5px 0;
                        border-bottom: 1px solid #d5d5d5;
                    }
                }
            }
            
        }
    }

    @media (max-width: 414px){
        .box-curriculo {
            .box-left {
                .topico {
                    .texto {
                        .empresa {
                            font-size: 13px;
                            .tit-atividades {
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
            .box-right {
                .box-topico2 {
                    padding: 0;
                }
            }
        }
    }
    @media (max-width: 1024px){
        .box-curriculo {
            .box-left {
                .topico {
                    flex-direction: column;
                }
            }
        }
    }
}
