.Footer {
    background: #404040;
    padding: 50px 0;
    .box-footer {
        border: 0px solid #000;
        display: flex;
        justify-content: space-between;
        a { text-decoration: none; }
        .box-icon-footer {
            border: 0px solid green;
            display: flex;
            min-width: 235px;

            .icon {
                border: 0px solid blue;
                width: 55px;
                height: 55px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
                background: #61cfd3;
            }
            .txt-item {
                font-size: 13px;
                padding: 10px;
                color: #ffffff;
            }
        }
    }
    .icon-whats {
        position: fixed;
        right: 20px;
        bottom: 40px;
        img {
            width: 55px;
        }
    }

    @media (max-width: 414px){
        .icon-whats img {
            width: 50px;
        }
        .box-footer {
            flex-direction: column;
        }
    }

    @media (max-width: 768px){
        .box-footer {
            flex-wrap: wrap;
            .box-icon-footer {
                padding: 10px 0;
            }
        }
    }
}

@media (max-width: 414px){
    .Footer {
        height: unset;
        padding: 10px 0;
    }
}