.Projeto {
    h1 {
        color: #606060;
        font-size: 25px;
    }
    .box-projeto {
        border-top: 1px solid #d5d5d5;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 40px 0 60px;
        .box-left {
            .box-galeria {
                position: relative;
                height: 300px;
                width: 560px;
            }
        }
        .box-right {
            flex: 1;
            .box-descricao {
                .title {
                    font-size: 20px;
                    color: #61CFD3;
                    font-weight: bold;
                    border: 0px solid blue;
                    padding: 15px 25px;
                }
                .descricao {
                    padding: 10px 25px;
                    line-height: 25px;
                }
                .circle {
                    width: 100px;
                    height: 100px;
                    background: #404040;
                    color: #ffffff;
                    font-size: 13px;
                    display: flex;
                    text-align: center;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    padding: 0 3px;
                }
            }
        }
        .lbox {
            visibility: hidden;
            opacity: 0;
            position: absolute;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            .box_img{
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    width: 75%
                }
                #close {
                    position: absolute;
                    top: 10%;
                    right: 10%;
                    color: #fff;
                }
                #prev {
                    position: absolute;
                    top: 50%;
                    left: 10%;
                    color: #ffffff;
                }
                #next {
                    position: absolute;
                    top: 50%;
                    right: 10%;
                    color: #ffffff;
                }
            }
        }
        .lbox:target{
            opacity: 1;
            visibility: visible;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            position: fixed;
            background: rgba(0,0,0,0.8);
            z-index: 100;
        }

        @media (max-width: 414px){
            .box-left {
                //min-width: 385px;
                .box-galeria {
                    &.w-sm {
                        width: 355px;
                        height: 165px;
                    }
                    .lbox {
                        .box_img {
                            #prev{
                                left: 4%;
                            }
                            #next {
                                right: 4%;
                            }
                        }
                    }
                }
            }
            .box-right {
                .box-descricao {
                    .title {
                        font-size: 18px;
                    }
                }
            }
        }
        @media (max-width: 768px){
            .box-right {
                .box-descricao {
                    .title {
                        text-align: center;
                    }
                }
            }
        }
    }
}
