.Menu {
    
    border-top: 15px solid #404040;
    padding: 20px 0;
    background: #ffffff;
    .menu-hambu{
        border: 0px solid #000;
        padding-top: 20px;
        .navbar {
            width: 55px;
            .navbar-nav {
                line-height: 2;
                a { 
                    color: #a5a5a5;
                }
                a:hover {
                    color: #61CFD3;
                }
            }
        }
    }
    .box-nome {
        display: flex;
        
        .dv-nome {
            padding: 15px;
            color: #606060;
            text-align: center;
            .nome {
                font-size: 40px;
                font-weight: bold;
                line-height: 1.2em;
            }
            .cargo {
                font-size: 16px;
            }
        }
        .dv-menu {
            flex: 1;
            padding: 15px;
            nav {
                margin-top: 30px;
            }
            ul {
                display: flex;
            }
            li {
                flex: 1;
                list-style: none;
                font-weight: bold;
                a {
                    color: #C3BFBE;
                    text-decoration: none;
                    font-size: 16px;
                    &.active {
                        color: #61CFD3;
                    }
                }
                .active {
                    color: red;
                }
                a:hover {
                    color: #61CFD3;
                }
            }
        }
    }

    @media (max-width: 414px){
        .box-nome {
            .dv-nome {
                .nome {
                    font-size: 30px;
                }
                .cargo {
                    font-size: 13px;
                }
            }
        }
        
    }
}