.Home {
    
    .box-home {
        display: flex;
        flex-wrap: wrap;
        .box-img {
            .img-foto {
                img {
                    border-radius: 50%;
                    width: 300px;
                }
            }
        }
        .box-right {
            flex: 1;
            padding: 0 80px;
            h1 {
                font-size: 45px;
                color: #606060;
                font-weight: bold;
            }
            p {
                font-size: 25px;
                color: #606060;
            }
            .box-circle {
                display: flex;
                padding: 15px 0;
                a {
                    text-decoration: none;
                    color: white;
                    font-weight: bold;
                }
                a:hover {
                    &.red { background: #606060; }
                    &.blue { background: #469ea1; }
                    &.yellow { background: #aaa31f; }
                }
                .circle-item {
                    width: 160px;
                    height: 160px;
                    border-radius: 50%;
                    margin: 0 5px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 14px;
                    box-shadow: 1px 1px 4px 1px #5e5c5c;
                    
                    &.red { background: #404040; }
                    &.blue { background: #61cfd3; }
                    &.yellow { background: #d1c825; }
                }
            }

            .box-texto {
                padding: 15px;
                max-width: 550px;
                p {
                    font-size: 13px;
                    color: #606060;
                }
            }
        }
    }

    @media (max-width: 414px){
        .box-home {
            .box-img {
                .img-foto {
                    img {
                        width: unset;
                    }
                }
            }
            .box-right {
                padding: 0;
                h1 {
                    font-size: 30px;
                    padding: 0 10px;
                }
                p {
                    font-size: 16px;
                    padding: 0 10px;
                }
                .box-circle {
                    .circle-item {
                        font-size: 12px;
                        width: 100px;
                        height: 100px;
                        text-align: center;
                    }
                }
            }
        }
        
    }

    @media (max-width: 1024px){
        .box-home {
            justify-content: center;
            .box-right {
                flex: 0;
            }
        }
    }
}